.hero {
    height: 95vh;
    max-height: 1100px;
    background: #0F1010 url("../images/hero.jpg") right center no-repeat;
    background-size: auto 100%;
    position: relative;

    @include mobile {
        height: 90vh;
        background-position: 80% center;
    }

    .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
    }

    p {
        color: $white;
        font-size: $fontLarge;
        font-family: "MonumentExtended", sans-serif;
        margin: 0;
        line-height: 1.1;

        @include mobile {
            font-size: $fontMedium;
        }

        @include smallMobile {
            font-size: $fontBase;
        }

        span {
            display: block;
        }
    }

    svg {
        position: absolute;
        bottom: $spaceLarge;
        left: calc(50% - 45px);
    }
}

.intro {
    padding: $spaceExtraLarge 0;
    text-align: center;
    background: transparent url("../images/intro-text-bg.svg") 97% center no-repeat;

    @include mobile {
        background-image: none;
    }

    @include smallDesktop {
        background-image: none;
    }

    h1 {
        font-weight: 300;
        line-height: 1.8;
        font-size: $fontMedium;

        @include mobile {
            font-size: $fontSmall;
        }
    }
}

.portfolio-links {
    display: block;
    li {
        border-top: 1px solid $black;

        &:last-of-type {
            border-bottom: 1px solid $black;
        }

        a {
            color: $black;
            font-family: "MonumentExtended", sans-serif;
            text-transform: uppercase;
            text-decoration: none;
            font-size: $fontMedium;
            display: block;
            padding: $spaceMedium 0;
            background: transparent url("../images/home-project-normal.svg") right center no-repeat;
            background-size: auto 68px;
            padding-right: 80px;

            &:hover {
                background-image: url("../images/home-project-hover.svg");
                background-size: auto 84px;
            }

            @include mobile {
                font-size: $fontBase;
            }
        }
    }
}

.services {
    text-align: center;
    padding-bottom: $spaceExtraLarge;
    background-image: url("../images/blue-dot.png"), url("../images/pink-dot.png"), url("../images/purple-dot.png"), url("../images/services-bg.svg");
    background-position: left top, right center, center bottom, 2% 87%;
    background-repeat: no-repeat;

    @include smallDesktop {
        background-image: url("../images/blue-dot.png"), url("../images/pink-dot.png"), url("../images/purple-dot.png");
        background-size: 80%;
    }

    h3 {
        font-size: $fontSemiLarge;
        margin-bottom: $spaceLarge;
        font-family: "AntipolExtended", sans-serif;

        @include mobile {
            font-size: $fontBase;
        }
    }

    .bg-text {
        background: $black;
        color: $white;
        padding: $spaceSmall $spaceBase;
        font-size: $fontMedium;
        font-family: "MonumentExtended", sans-serif;

        @include mobile {
            font-size: $fontBase;
        }
    }
}

.services-steps {
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    text-align: left;
    margin: $spaceExtraLarge*2 0;

    li {
        width: 50%;
        display: flex;

        @include mobile {
            width: 100%;
            margin-bottom: $spaceExtraLarge;
        }

        &:nth-child(2) {
            align-self: flex-end;
        }

        .number {
            width: 90px;
            height: 90px;
            border-radius: 50%;
            background: $black;
            color: $white;
            text-align: center;
            line-height: 94px;
            flex-shrink: 0;
            margin-right: $spaceBase; 
            font-family: "MonumentExtended", sans-serif;
            font-size: $fontMedium;

            @include mobile {
                width: 60px;
                height: 60px;
                line-height: 64px;
                font-size: $fontBase;
            }
        }

        h4 {
            margin: 0 0 $spaceSmall 0;
            font-family: "AntipolExtended", sans-serif;
            line-height: 1.4;
        }

        p {
            margin: 0;

            @include mobile {
                font-size: $fontSmall;
            }
        }
    }
}

.services-list {
    display: flex;

    @include mobile {
        flex-direction: column;
    }

    div {
        flex: 1;

        @include mobile {
            margin-bottom: $spaceExtraLarge;
        }

        strong {
            font-size: 34px;
        }
    }

    ul {
        display: block;

        li {
            margin-top: $spaceSmall;
        }
    }
}

.home-footer {
    .content {
        border: none;
        padding-top: $spaceExtraLarge;
        background-image: url("assets/images/star.svg");
        background-position: 95% center;
        background-repeat: no-repeat;

        @include mobile {
            background-position: 95% 10%;
            padding-top: 0;
        }
    }

    p {
        text-align: left;
        font-size: $fontSemiLarge;
        font-family: "MonumentExtended", sans-serif;
        padding-top: $spaceLarge;
        border-top: 1px solid $white;
        line-height: 1.4;
        margin-bottom: $spaceMedium;

        @include mobile {
            font-size: $fontBase;
        }
    }

    a {
        color: $white;
    }

    span {
        color: $white;
        display: block;
        margin-top: $spaceBase;
    }
}