header {
    color: $white;
    position: absolute;
    top: 0;
    width: 100%;
    padding: $spaceLarge 0;
    z-index: 99;

    .content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.7em;
    }

    ul li {
        margin-left: $spaceBase;
    }
}

.light-header .logo,
.light-header .white-link {
    color: $black;
}