// General style
@font-face {
    font-family: MonumentExtended;
    src: url("../fonts/MonumentExtended-Regular.otf");
}

@font-face {
    font-family: IBMPlexSans;
    src: url("../fonts/IBMPlexSans-Regular.ttf");
}

@font-face {
    font-family: AntipolExtended;
    src: url("../fonts/antipol-extended-medium.otf");
}

body {
    color: $white;
    font-size: $fontBase;
    line-height: 1.4;
    margin: 0;
    font-family: "IBMPlexSans", sans-serif;
    background: $black;

    &.light-bg {
        background: $lightGrey;
        color: $black;
    }
}

p {
    line-height: 1.8;

    @include mobile {
        font-size: $fontSmall;
    }
}

.content {
    max-width: 1160px;
    margin: 0 auto;
    padding: 0 $spaceMedium;

    @include mobile {
        padding: 0;
        margin: 0 $spaceMedium;
    }
}

.white-link, .logo {
    color: $white;
    text-decoration: none;
}

.logo {
    font-family: "MonumentExtended", sans-serif;
    text-transform: uppercase;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
}

.cover {
    height: 360px;
    text-align: center;
    margin-top: 129px;
    display: flex;

    @include mobile {
        height: 220px;
    }

    .content {
        border-bottom: 1px solid $lightGrey;
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: center;
        padding-bottom: $spaceMedium;
        position: relative;
    }

    h1 {
        margin: 0;
        line-height: 1.2;
        font-size: $fontLarge;
        font-family: "MonumentExtended", sans-serif;
        text-transform: uppercase;

        @include mobile {
            font-size: $fontBase;
        }
    }
}

.light-bg .cover .content {
    border-color: $black;
}