footer {
    background: $black;
    padding: $spaceExtraLarge 0;
    font-size: $fontSmall;

    .content {
        border-top: 1px solid $white;
    }

    ul {
        justify-content: center;
        padding: $spaceLarge 0;

        @include mobile {
            flex-direction: column;
            align-items: center;
            padding: $spaceMedium;
        }

        li {
            margin-right: $spaceBase;

            @include mobile {
                margin-right: 0;
            }

            &::after {
                margin-left: $spaceSmall;
                content: "-";
                color: $white;

                @include mobile {
                    display: none;
                }
            }

            &:last-child {
                margin-right: 0;
            }

            &:last-child::after {
                content: none;
            }
        }
    }

    p {
        color: $white;
        text-align: center;
        margin: 0;
    }
}