// Colors
$white: #EAEAEA;
$lightGrey: #E5E5E5;
$black: #1F2020;

// Fonts
$fontSmall: 20px;
$fontBase: 34px;
$fontMedium: 52px;
$fontSemiLarge: 64px;
$fontLarge: 96px;

// Spaces
$spaceSmall: 8px;
$spaceBase: 16px;
$spaceMedium: 32px;
$spaceLarge: 48px;
$spaceExtraLarge: 64px;

// Breakpoints
@mixin smallMobile {
    @media (max-width: 420px) {
        @content
    }
};

@mixin mobile {
    @media (max-width: 767px) {
        @content
    }
};

@mixin tablet {
    @media (max-width: 1024px) {
        @content
    }
};

@mixin smallDesktop {
    @media (max-width: 1280px) {
        @content
    }
}