.page-content {
    padding: $spaceExtraLarge 0;
    text-align: center;

    h3 {
        font-size: $fontLarge;
        font-family: "AntipolExtended", sans-serif;
        margin-top: 0;

        @include mobile {
            font-size: $fontBase;
        }
    }

    figure {
        display: flex;
        justify-content: space-around;
        margin: $spaceExtraLarge auto;

        @include tablet {
            flex-direction: column;
        }

        img:first-of-type {
            @include tablet {
                margin-bottom: $spaceLarge;
            }
        }
    }
}

.table {
    display: flex;
    padding-top: $spaceExtraLarge*2;
    font-size: $fontBase;
    border-top: 1px solid $lightGrey;

    @include mobile {
        padding-top: $spaceExtraLarge;
    }

    &:last-of-type {
        border: none;
        padding-bottom: $spaceExtraLarge*2;

        @include mobile {
            padding-bottom: $spaceExtraLarge;
        }
    }

    @include mobile {
        flex-direction: column;
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    h4 {
        width: 30%;
        margin: 0;

        @include mobile {
            margin-bottom: $spaceMedium;
        }
    }

    ul {
        display: block;
  
        li {
            margin-bottom: $spaceMedium;

            @include mobile {
                font-size: $fontSmall;
            }

            &:last-of-type {
                margin-bottom: 0;
            }

            em {
                display: block;
            }
        }
    }
}