.work-dates {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: $fontMedium;
    font-family: "AntipolExtended", sans-serif;
    font-style: italic;

    @include mobile {
        font-size: $fontSmall;
    }
}

.work-link {
    color: $black;
    text-decoration: none;

    &:last-of-type .work {
        border-bottom: none;
    }
}

.work {
    padding: $spaceExtraLarge 0;
    border-bottom: 1px solid $black;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;

    &:hover figure {
        transform: scale(1.05);
    }

    .description, a {
        font-size: $fontSmall;
        display: block;
        font-weight: 200;
    }    

    .title {
        font-size: $fontMedium;
        font-family: "MonumentExtended", sans-serif;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;

        @include mobile {
            font-size: $fontBase;
            position: static;
            transform: none;
            line-height: 1.1;
            margin-top: $spaceBase;
        }
    }

    .info {
        display: flex;
        justify-content: space-between;
        font-size: $fontSmall;

        @include mobile {
            display: block;
        }

        span {
            display: block;
        }

        .hidden-mb {
            @include mobile {
                display: none;
            }
        } 

        em {
            font-family: "AntipolExtended", sans-serif;
        }
    }

    figure {
        width: 800px;
        height: 470px;
        background: #A3C8FF; 
        align-self: center;
        margin: $spaceLarge 0;
        transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1);

        @include mobile {
            width: 100%;
            height: 300px;
            margin: $spaceMedium 0;
        }

        img {
            @include mobile {
                max-width: 100%;
                max-height: 100%;
            }
        }
    }
}

.project-detail {
    flex-direction: column;
    text-align: left;
    font-size: $fontSmall;

    li {
        display: flex;
        margin-bottom: $spaceSmall;
    }

    .detail-name {
        width: 200px;
    }

    .detail-value {
        flex: 1;
    }
}

.project-intro, .project-outro {
    font-size: $fontSmall;

    .content {
        display: flex;
        justify-content: space-between;
    }

    figure {
        width: 466px;
        height: 646px;
        background: #A3C8FF;
        flex-shrink: 0;
        margin: 0 $spaceExtraLarge*2 0 0;
    }

    h3 {
        font-size: $fontBase;
        margin-bottom: $spaceSmall;
    }

    p {
        margin-top: 0;
        line-height: 1.6;
    }
}

.project-outro {
    figure {
        width: 763px;
        height: 538px;
        background: #A3C8FF;
        margin: 0 0 0 $spaceLarge;
    }
}

.horizontal-slider {
    margin: $spaceExtraLarge 0;
    display: flex;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;

    .slide {
        margin: 0 $spaceLarge 0 0; 
        scroll-snap-align: center;

        &:first-of-type {
            margin-left: 50vw;
        }

        &:last-of-type {
            margin-right: 50vw;
        }
    }
}

.two-cols {
    font-size: $fontSmall;
    margin: $spaceExtraLarge*2 0;

    .content {
        display: flex;

        div {
            flex: 1;

            &:first-of-type {
                margin-right: $spaceMedium;
            }

            &:last-of-type {
                margin-left: $spaceMedium;
            }

            ul {
                flex-direction: column;
                list-style: disc;
                padding-left: $spaceMedium;
            }
        }
    }
}

.project-footer {
    margin-top: $spaceExtraLarge*2;

    p {
        font-family: "MonumentExtended", sans-serif;
        line-height: 1.4;
    }
}